<template>
<div class="sidebar">
    <div class="accordion" id="accordionExample">
    <!-- popular starts herer -->
    <div class="card" v-if="popular.length > 0">
    <div class="card-header">
      <h5 class="card-link" data-toggle="collapse show" href="#popular">
       Popular
      </h5>
    </div>
    <div id="popular" class="collapse show" data-parent="#accordion">
      <div class="card-body">
        <li class="list-group-item" v-for="league in popular">
              <img src="/flags/africa.svg" class="world-logo" v-if="joinNames(league.region)==='Africa'">
              <img src="/flags/International.svg" class="world-logo" v-else-if="joinNames(league.region)==='Americas'">
                <img src="/flags/International.svg" class="world-logo" v-else-if="joinNames(league.region)==='UEFAChampionsLeagueQualification'">
              <img v-else src="/flags/blank.gif" :class="'mr-2 flag flag-' + joinNames(league.region)" alt=""/>
             {{resizeName(league.name)}}
        </li>
         <!-- international -->
         <li class="list-group-item" v-if="international.length > 0">
             <h5 data-toggle="collapse" href="#international">
            <img src="/img/international.svg" class="world-logo"> International</h5>
             </li>


    <div id="international" class="collapse" data-parent="#accordion">
      <div class="card-body">
        <li class="list-group-item" v-for="game in international">{{game.name}}
        </li>
      </div>
    </div>
  </div>
  <!-- international -->
      </div>
    </div>
  </div>



  <!-- popular ends here -->
  <div class="card z-depth-0 bordered" v-for="(sport,index) in sports" :key="index">
    <div class="card-header" id="sportData">
      <h5 class="mb-0">
        <button class="btn btn-link" type="button" data-toggle="collapse" v-if="sport.categories.length > 1" :data-target="`#${joinNames(sport.name)}`" aria-expanded="true" aria-controls="collapseOne">
          <!-- <img class="sport-logo" :src="`/flags/${joinNames(sport.name)}.png`" alt="">{{sport.name}} -->


          <span v-if="sport.name ==='Football'"><i class="fa fa-lg fa-futbol"></i></span>
          <span v-if="sport.name ==='Basketball'"><i class="fa fa-lg fa-basketball-ball"></i></span>
          <span v-if="sport.name ==='Rugby Union'"><i class="fas fa-football-ball"></i></span>
          {{sport.name}}
        </button>
      </h5>
    </div>
    <div :id="joinNames(sport.name)" class="collapse" aria-labelledby="sportData" data-parent="#sports">
      <div class="card-body">

          <!-- categories here -->
          <div class="accordion" id="accordionExample">
                <div class="card z-depth-0 bordered" v-for="(category,index) in popWorld(sport.categories)" :key="index">
                    <div class="card-header" id="sportData">
                    <h5 class="mb-0">
                        <button class="btn btn-link" type="button" data-toggle="collapse" :data-target="`#${joinNames(category.name)+ joinNames(sport.name)}`"
                        aria-expanded="true" aria-controls="collapseOne" v-if="category.competitions.length > 0">
                        <img src="/flags/blank.gif" :class="'mr-2 flag flag-' + joinNames(category.name)" alt=""/>{{category.name}}
                        <span class="float-right">{{category.competitions.length}}</span>
                        </button>
                    </h5>
                    </div>
                    <div :id="joinNames(category.name)+ joinNames(sport.name)" class="collapse" aria-labelledby="sportData" data-parent="#sports">
                    <div class="card-body">
                    <li v-for="tournament in category.competitions" class="list-group-item">
                        {{tournament.name}} <span>{{tournament.length}}</span>
                    </li>
                    </div>
                    </div>
                </div>

                </div>


          <!-- categories ends here -->

      </div>
    </div>
  </div>

</div>
</div>


</template>

<script>

import {TEST_SPORTS, TEST_POPULAR, TEST_INTERNATIONAL} from '../../api_routes'
import axios from 'axios'
export default {

  data () {
    return {
      sports: [],
      popular: [],
      international: []

    }
  },
  created () {
    this.fetchSports()
    this.fetchPopular()
    this.fetchInternational()
  },
  methods: {
    joinNames (names) {
      return names.split(' ').join('')
    },
    resizeName (title) {
      let shortName = title.split(' ').slice(0, 2)
      return shortName.toString()
    },
    fetchSports () {
      axios.get(TEST_SPORTS).then(response => {
        console.log('sport', response.data.data)
        let sports = response.data.data
        this.sports = this.filterSports(sports)
      })
    },

    fetchPopular () {
      axios.get(TEST_POPULAR).then(resp => {
        this.popular = resp.data.data
      })
    },
    fetchInternational () {
      axios.get(TEST_INTERNATIONAL).then(resp => {
        this.international = resp.data.data
      })
    },

    filterSports (sports) {
      let filtered = sports.filter((s, index) => {
        return s.categories.length > 1
      })

      return filtered
    },
    popWorld (categories) {
      let cats = categories.filter((category, index) => {
        return category.name !== 'World' && category.competitions.length > 0
      })
      console.log(cats)
      return cats
    }
  }

}
</script>

